header {
  background-image: url(/hiking-project/static/media/nature2-small.350eaf5a.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  padding: 20px 0;
  text-align: center;
  text-shadow: 1px 1px #185b64;
