    header form img {
      height: 15px;
      width: 15px;
      margin-right: 10px; }
  header #searchBox {
    opacity: 0.8;
    width: 100%;
    margin: 0; }
  header #searchBox:hover {
    opacity: 1; }
  header input {
    border-radius: 2em;
