.card {
  margin: 10px auto;
  flex-basis: 300px; }
  .card .hoverText {
    position: relative;
    height: 200px; }
    .card .hoverText img {
      display: block;
      width: 100%;
      height: 200px; }
    .card .hoverText p {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      text-align: center;
      padding: 3em 1em;
      opacity: 0;
      height: 200px;
      -webkit-transition: .5s ease;
      transition: .5s ease;
      background-color: rgba(24, 91, 100, 0.5);
      color: white; }
  .card .hoverText:hover p {
    opacity: 1; }
  .card button {
    margin-left: 5px; }
  .card .card-saved {
    margin-left: auto;
    margin-right: auto;
    margin-top: 2em; }

.card-container {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 1em; }
  .card-container .row {
    padding: 0;
    margin: 0; }

.rating img {
  height: 15px;
  width: 15px; }

.diff img {
  height: 15px;
  width: 15px; }

.btn {
  color: white;
  margin-top: 1em; }

.hikemap {
  display: flex; }
  .hikemap .card {
    flex-basis: 400px;
    height: inherit;
    border: none; }
    .hikemap .card img.p-3 {
