main {
  box-sizing: border-box;
  padding: 20px 0; }

.leaflet-container {
  height: 400px;
  width: 100%; }

.container {
  padding: 0;
  display: flex;
  overflow: hidden;
  height: 110vh;
  position: relative;
  width: 100%;
  flex-wrap: wrap;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  will-change: overflow; }
  .container #map, .container #card {
    overflow: auto;
    height: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none; }
  .container #map {
    height: 50%;
    width: 100%; }
  .container #card {
    height: 50%;
    overflow: scroll; }

.pagination-holder {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 3.5em;
  margin: 1em;
  padding: 0;
  background-color: white; }
  .pagination-holder li {
    padding: 1em 0.8em; }
  .pagination-holder li.first {
    display: none; }
  .pagination-holder li.last {
    display: none; }
  .pagination-holder li.active a {
    background-color: #185b64;
