@media (min-width: 768px) {
  .container {
    padding: 0;
    display: flex;
    overflow: hidden;
    height: 70vh;
    position: relative;
    width: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    will-change: overflow; }
    .container #map, .container #card {
      overflow: auto;
      height: auto;
      -webkit-overflow-scrolling: touch;
      -ms-overflow-style: none; }
    .container #map {
